  .fadein {
    -webkit-animation: fadein .5s;
       -moz-animation: fadein .5s;
        -ms-animation: fadein .5s;
         -o-animation: fadein .5s;
            animation: fadein .5s;
  }
  @keyframes fadein {from { opacity: 0; }to   { opacity: 1; }}
  @-moz-keyframes fadein {from { opacity: 0; }to   { opacity: 1; }}
  @-webkit-keyframes fadein {from { opacity: 0; }to   { opacity: 1; }}
  @-ms-keyframes fadein {from { opacity: 0; }to   { opacity: 1; }}
  @-o-keyframes fadein {from { opacity: 0; }to   { opacity: 1; }}
  .fadeout {
    -webkit-animation: fadeout .5s;
    -moz-animation: fadeout .5s;
    -ms-animation: fadeout .5s;
    -o-animation: fadeout .5s;
    animation: fadeout .5s;
    opacity: 0;
  }
  @keyframes fadeout {from { opacity: 1; }to   { opacity: 0; }}
  @-moz-keyframes fadeout {from { opacity: 1; }to   { opacity: 0; }}
  @-webkit-keyframes fadeout {from { opacity: 1; }to   { opacity: 0; }}
  @-ms-keyframes fadeout {from { opacity: 1; }to   { opacity: 0; }}
  @-o-keyframes fadeout {from { opacity: 1; }to   { opacity: 0; }}
  .progress{
    transition: stroke-dasharray 1s;
  }
  .progressdelay{
    animation: 1s pd;
    -webkit-animation: pd 1s;
    -moz-animation: pd 1s;
    -ms-animation: pd 1s;
    -o-animation: pd 1s;
    fill: rgb(0, 146, 255)
  }
  @keyframes pd {0% { opacity: 0.4; fill:rgb(255, 255, 255) }99% { opacity: 0.4; fill:rgb(255, 255, 255) }100% { opacity: 1 }}
  @-moz-keyframes pd {0% { opacity: 0.4; fill:rgb(255, 255, 255) }99% { opacity: 0.4; fill:rgb(255, 255, 255) }100% { opacity: 1 }}
