  @-o-keyframes pd {0% { opacity: 0.4; fill:rgb(255, 255, 255) }99% { opacity: 0.4; fill:rgb(255, 255, 255) }100% { opacity: 1 }}
  .normaldots{
    fill: rgb(255, 255, 255);
    opacity: 0.4;
  }
